import React, {
  useState,
  useContext
} from 'react';
import styled from 'styled-components';
import { DatePicker } from 'antd';
import { PlusSquareFilled } from '@ant-design/icons';
import moment, { Moment } from 'moment';
import { observer } from 'mobx-react';
import { ToDoStoreCtx, FORMAT } from '../../store/toDoStore';
import ToDoAutoComplete from './ToDoAutoComplete';
import { composeIconUrl } from '../../utils';


const InputWrapper = styled.div`
  position: relative;
  display: flex;
  width: 100%;
  .ant-select {
    width: 70%;
    margin: 10px;
    min-width: 50px;
    .ant-select-selector {
      height: 40px;
      span {
        padding-top: 2px;
        font-size: ${props => `calc(${props.theme.font.sm}*0.9)`};
      }
    }
  }
  .ant-picker {
    height: 41px;
    margin: 10px;
    width: 30%;
    min-width: 30px;
    .ant-picker-input input{
      font-size: ${props => `calc(${props.theme.font.sm}*0.7)`};
    }
  }
  .anticon-plus-square{
    height: 40px;
    margin: 10px;
    font-size: 40px;
    cursor: pointer;
  }
`;

const DataCell = styled.div`
  &:before {
    border: none !important;
  }
`;

const AddBtn = styled(PlusSquareFilled)`
`;

const ToDoInput = ({ setDate, date }:
{
  setDate: (date: Moment) => void;
  date: Moment;
}) => {

  const toDoStore = useContext(ToDoStoreCtx);
  const [pecs, setPecs] = useState<TodoPecs | null>(null);
  const [value, setValue] = useState<string>('');

  const handleChange = (newDate: Moment | null) => {
    if (newDate === null) return;
    setDate(newDate);
  };

  const handleAddToDo = () => {
    const formatedDate = date.format(FORMAT);

    if (pecs) {
      // default pecs
      if (pecs.background) {
        toDoStore.addToDo(
          formatedDate,
          value,
          pecs.imageId,
          pecs.background
        );
      // uploaded pecs
      } else {
        toDoStore.addToDo(
          formatedDate,
          value,
          composeIconUrl(pecs.imageId),
          undefined
        );
      }
    // no pecs
    } else {
      toDoStore.addToDo(
        formatedDate,
        value,
        undefined,
        undefined
      );
    }
  };

  return (
    <InputWrapper>
      <DatePicker
        value={moment(date)}
        allowClear={false}
        onChange={(newDate: Moment | null) => handleChange(newDate)}
        dateRender={cellDate => {
          const style: React.CSSProperties = {};
          const cellDateString = cellDate.format(FORMAT);
          if (toDoStore.toDoList
            && cellDateString in toDoStore.toDoList
            && cellDateString !== date.format(FORMAT)) {
            style.background = 'red';
            style.border = 'none';
            style.color = 'white';
          }
          return (
            <DataCell className="ant-picker-cell-inner" style={style}>
              {cellDate.date()}
            </DataCell>
          );
        }}
      />
      <ToDoAutoComplete
        setPecs={setPecs}
        setValue={setValue}
        pecs={pecs}
        value={value}
      />
      <AddBtn onClick={handleAddToDo} />
    </InputWrapper>
  );
};

export default observer(ToDoInput);
