/* eslint-disable no-param-reassign */
import React, { ReactNode } from 'react';
import {
  CalendarFilled,
  CalendarOutlined,
  CloudOutlined,
  CloudFilled,
  SoundFilled,
  SoundOutlined,
  VideoCameraFilled,
  VideoCameraOutlined
} from '@ant-design/icons';
import store from 'store';
import GridLayout from 'react-grid-layout';
import WeatherWidget from '../../../components/weatherWidget';
import MusicWidget from '../../../components/musicWidget';
import VideoWidget from '../../../components/videoWidget';
import ToDoWidget from '../../../components/todoWidget';
import { API, StoreKeys } from '../../../assets/const';

enum Widgets {
  WEATHER = 'weather',
  TODO = 'todo',
  MUSIC = 'music',
  VIDEO = 'video'
}

interface WidgetConfig {
  [k: string]: {
    component?: ReactNode;
    initialLayout: {
      x: number;
      y: number;
      w: number;
      h: number;
      isResizable: boolean;
      static: boolean;
    };
    selected: boolean;
    iconSelected?: ReactNode;
    iconUnSelected?: ReactNode;
  }
}

// key for local storage
const configKey = StoreKeys.widgetConfig;

// default layout
const defaultWidgetConfig: WidgetConfig = {
  [Widgets.WEATHER]: {
    initialLayout: {
      x: 0,
      y: 0,
      w: 40,
      h: 20,
      isResizable: false,
      static: true
    },
    selected: true,
  },
  [Widgets.TODO]: {
    initialLayout: {
      x: 0,
      y: 0,
      w: 40,
      h: 60,
      isResizable: false,
      static: true
    },
    selected: true,
  },
  [Widgets.MUSIC]: {
    initialLayout: {
      x: 40,
      y: 0,
      w: 24,
      h: 80,
      isResizable: false,
      static: true
    },
    selected: true,
  },
  [Widgets.VIDEO]: {
    initialLayout: {
      x: 0,
      y: 80,
      w: 40,
      h: 60,
      isResizable: false,
      static: true
    },
    selected: true,
  }
};


/** 
 * This method is used in widgetPage.tsx
 *
 * Compose selected widgets' layout with updated layouts (e.g. resized & postion changed)
 * (NOTE: selected widgets are the widgets will be displayed)
 * 
 * The reason we need to compose is because:
 *   1. Selected state is managed by custom hooks (useState) 
 *   2. Layout state is managed by 'react-grid-layout'
 *   And we need both state to manage the display.
 * 
 * And save updated layout to local storage
 *
 * @param config: WidgetConfig
 * @params layouts: Gridlayout.Layout[]
 */
export const saveLayout = (
  config: WidgetConfig,
  layouts: GridLayout.Layout[]
) => {
  const composedConfig: WidgetConfig =
    Object
      .keys(defaultWidgetConfig) // iterate through widget names
      .reduce((configToCompose, widgetKey): WidgetConfig => {
        const layout = layouts.filter(l => l.i === widgetKey)[0];

        configToCompose[widgetKey] = {
          selected: config[widgetKey].selected,
          initialLayout:
            layout
              // if layout exist then overwrite
              ? {
                x: layout.x,
                y: layout.y,
                w: layout.w,
                h: layout.h,
                isResizable: layout.isResizable as boolean,
                static: layout.static as boolean
              }
              // if layout not exist then use default layout
              : {
                ...defaultWidgetConfig[widgetKey].initialLayout
              }
        };

        return configToCompose;
      }, {} as WidgetConfig);
  /* eslint-disable no-param-reassign */

  // save to local storage
  store.set(configKey, JSON.stringify(composedConfig));
};

const initWidgetLocalStorage = () => {
  // save to local storage
  store.set(configKey, JSON.stringify(defaultWidgetConfig));
  return store.get(configKey);
};

// check if exist config in local storage
let config = store.get(configKey);
if (!config) config = initWidgetLocalStorage();
config = JSON.parse(config);

// finalize widgets with component
// export with config loaded from local storage
export const widgets: WidgetConfig = {
  [Widgets.WEATHER]: {
    component: <WeatherWidget apiKey={API.OPENWEATHERMAP_API_KEY} />,
    ...config[Widgets.WEATHER],
    iconSelected: <CloudFilled />,
    iconUnSelected: <CloudOutlined />
  },
  [Widgets.TODO]: {
    component: <ToDoWidget />,
    ...config[Widgets.TODO],
    iconSelected: <CalendarFilled />,
    iconUnSelected: <CalendarOutlined />
  },
  [Widgets.MUSIC]: {
    component: <MusicWidget />,
    ...config[Widgets.MUSIC],
    iconSelected: <SoundFilled />,
    iconUnSelected: <SoundOutlined />
  },
  [Widgets.VIDEO]: {
    component: <VideoWidget />,
    ...config[Widgets.VIDEO],
    iconSelected: <VideoCameraFilled />,
    iconUnSelected: <VideoCameraOutlined />
  },
};