/* eslint-disable @typescript-eslint/no-explicit-any */
import React, { useRef } from 'react';
import styled from 'styled-components';
import { Carousel } from 'antd';
import ProfileSider from './profileSider';
import {
  ClientProfileSection,
  UserProfileSection
} from './profileSections';

const Wrapper = styled.div`
  background-color: lightgray;
  height: ${props => props.theme.content.height};
  min-width: ${props => props.theme.app.minWidth};
  display: flex;

  .ant-carousel {
    min-width: ${props => `calc(${props.theme.app.minWidth} * 0.75)`};
    width: 100%;
    background: #e2e2e2;
    height: 100%;

    .slick-slider {
        height: 100%;
    }

    .slick-slide {
        text-align: center;
        background: rgb(250, 251, 252);
        overflow: auto;
    }

    .slick-slide h3 {
        color: #fff;
    }

    .slick-list {
        height: 100%;

        .slick-track {
            height: 100%;
        }
    }
  }
`;

const ProfilePage = () => {
  const carouseEl = useRef(null);

  return (
    <Wrapper>
      <ProfileSider
        goTo={(index: number) => {
          (carouseEl as React.MutableRefObject<any>).current.goTo(index);
        }}
      />
      <Carousel
        infinite={false}
        dotPosition='bottom'
        ref={carouseEl}
        swipe={false}
      >
        <ClientProfileSection />
        <UserProfileSection />
      </Carousel>
    </Wrapper>
  );
};

export default ProfilePage;
