/* eslint-disable no-underscore-dangle */
import React, { useContext, useEffect } from 'react';
import styled from 'styled-components';
import { Card } from 'antd';
import {
  DeleteOutlined
} from '@ant-design/icons';
import { observer } from 'mobx-react';
import { PecsStoreCtx } from '../../store/pecsStore';
import { SectionHeader } from '../styled';
import { PecsForm } from '../forms';
import { composeIconUrl } from '../../utils';


const { Meta } = Card;

const PecsManageContent = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;

  .ant-card-cover {
    height: 300px;
  }
  .ant-card-body {
    padding: 18px;
  }
`;

const cardStyle = {
  width: '300px',
  height: '400px',
  margin: '5px 5px 5px 5px'
};

function PecsManagement() {
  const { pecsArr, removePecs, getPecs } = useContext(PecsStoreCtx);

  const handleDelete = async (pecs: Pecs) => {
    await removePecs({ id: pecs._id, imageId: pecs.imageId });
  };

  useEffect(() => {
    getPecs();
  }, [getPecs]);

  return (
    <>
      <SectionHeader>
        <h1> Pecs Management </h1>
        <PecsForm />
      </SectionHeader>
      <PecsManageContent>
        {pecsArr.map(pecs => {
          if (pecs.background) {
            return (
              <Card
                hoverable
                style={cardStyle}
                cover={
                  <img
                    alt="pecs"
                    src={pecs.imageId}
                    style={{
                      height: '100%',
                      background: pecs.background
                    }}
                  />
                }
                key={pecs._id}
                actions={[
                  // <PecsForm
                  //   id={pecs._id}
                  //   imageId={pecs.imageId}
                  //   name={pecs.name}
                  //   key="pecs"
                  // />
                ]}
              >
                <Meta title={pecs.name} />
              </Card>
            );
          } 
          return (
            <Card
              hoverable
              style={cardStyle}
              cover={
                <img
                  alt="pecs"
                  src={composeIconUrl(pecs.imageId)}
                  style={{
                    height: '100%'
                  }}
                />
                }
              key={pecs._id}
              actions={[
                <PecsForm
                  id={pecs._id}
                  imageId={pecs.imageId}
                  name={pecs.name}
                  key="pecs"
                />,
                <DeleteOutlined
                  key="delete"
                  onClick={() => handleDelete(pecs)}
                />
              ]}
            >
              <Meta title={pecs.name} />
            </Card>
          );
          
        }

        )}
      </PecsManageContent>
    </>
  );
}

export default observer(PecsManagement);
