import React, { useContext, useCallback, Fragment } from 'react';
import styled from 'styled-components';
import { CalendarOutlined } from '@ant-design/icons';
import { Moment } from 'moment';
import { observer } from 'mobx-react';
import Checkbox from './ToDoCheckbox';
import { ToDoStoreCtx, FORMAT } from '../../store/toDoStore';

const ToDoListItemWrapper = styled.div`
  position: relative;
  text-align: left;
  width: 100%;
  overflow-y: hidden;
  overflow-x: auto;
  p {
    font-size: 1.5em;
    margin-bottom: 0px;
    color: black;
    word-wrap: break-word;
    width: 100%;
  }
`;

const DateWrapper = styled.span`
  color: #cd0a0a;
`;

const PecWrapper = styled.img`
  height: 100px;
  width: 100px;
`;

// it is re-used in both flow view and list view
export const ToDoListItem = observer(({ date, id }: {
  date: Moment,
  id: number
}) => {
  const toDoStore = useContext(ToDoStoreCtx);
  const dateString = date.format(FORMAT);
  const renderItem = (): React.ReactNode | null => {
    if (!toDoStore.toDoList) return null;

    const targetTodo = toDoStore.toDoList[dateString]
      .filter((todo) => todo.id === id)[0];

    const renderPecs = () => {
      const { pecsUrl } = targetTodo;
      const { pecsBackground } = targetTodo;
      if (pecsUrl) {
        if (pecsBackground) {
          return (
            <PecWrapper
              alt="pecs"
              src={pecsUrl}
              style={{
                background: pecsBackground
              }}
            />
          );
        } 
        return (
          <PecWrapper
            alt="pecs"
            src={pecsUrl}
          />
        );
        
      }
      return null;
    };

    return (
      <>
        {renderPecs()}
        <p>{targetTodo.text}</p>
      </>
    );
  };

  return (
    <ToDoListItemWrapper>
      <Checkbox
        id={id.toString()}
        onCheck={() => {
          toDoStore.removeToDo(dateString, id);
        }}
      >
        {renderItem()}
      </Checkbox>

      <DateWrapper>
        <CalendarOutlined />
        {` ${dateString}`}
      </DateWrapper>
    </ToDoListItemWrapper>
  );
});


const ToDoListWrapper = styled.div`
  display: flex;
  flex-direction: column;
  width: 80%;
  background: rgba(255,255,255,0.5);
  padding: 0 10px;
  hr {
    opacity: 0.3;
    width: 100%;
  }
`;

const ToDoList = ({ date }: { date: Moment }) => {
  const toDoStore = useContext(ToDoStoreCtx);
  const renderItems = useCallback(() => {
    const dateString = date.format(FORMAT);
    return toDoStore.toDoList
      && toDoStore.toDoList[dateString]?.map((todo) =>
        <Fragment key={todo.id}>
          <ToDoListItem
            date={date}
            id={todo.id}
          />
          <hr />
        </Fragment>);
  }, [date, toDoStore.toDoList]);

  return (
    <ToDoListWrapper>
      {renderItems()}
    </ToDoListWrapper>
  );
};

export default observer(ToDoList);
