/* eslint-disable no-underscore-dangle */
import React, { useState, useContext } from 'react';
import styled from 'styled-components';
import { AutoComplete } from 'antd';
import { observer } from 'mobx-react';
import { PecsStoreCtx } from '../../store/pecsStore';
import { fuzzyStringRank, composeIconUrl } from '../../utils';

const { Option } = AutoComplete;

// used to split pecs _id, name and current input value to make option unique
const splitSymbol = '^';

const PecsWrapper = styled.img`
  height: 100px;
  width: 100px;
`;

const PecsPreview = styled.img`
  height: 40px;
  width: 40px;
  margin: 10px 0;
`;

const Description = styled.p`
  color: gray;
  font-weight: 500;
  font-size: 20px;
  padding-top: 10px;
`;

interface Props {
  value: string;
  pecs: TodoPecs | null;
  setPecs: React.Dispatch<React.SetStateAction<TodoPecs | null>>;
  setValue: React.Dispatch<React.SetStateAction<string>>;
}

// this element is outside the scope
// config this class in app.css
const dropdownClassName = 'auto-complete-dropdown';

function ToDoAutoComplete({
  value,
  pecs,
  setPecs,
  setValue
}: Props) {
  const [result, setResult] = useState<TodoPecs[]>([]);
  const { pecsArr } = useContext(PecsStoreCtx);
  const handleSearch = (input: string) => {
    if (pecs) setPecs(null);

    // rank input value with pecs' keys by leven distance
    const rankedPecsArr = fuzzyStringRank(input, pecsArr);
    const composedPecsArr = rankedPecsArr.map(p => {
      return {
        value: p._id + splitSymbol + p.name + splitSymbol + input, // combined value -> "_id^name^input"
        name: p.name, // name of pecs
        imageId: p.imageId, // image src of pecs
        background: p.background,
        _id: p._id
      };
    });

    setResult(composedPecsArr);
  };

  const handleSelect = (input: string) => {
    const [id, n, v] = input.split(splitSymbol);

    setPecs({
      value: v, // combined value -> "_id^name^input"
      name: n, // name of pecs
      imageId: pecsArr.filter(p => p._id === id)[0].imageId, // image src of pecs
      background: pecsArr.filter(p => p._id === id)[0].background,
      _id: id
    });
  };

  const renderPecsWrapper = (curPecs: TodoPecs) => {
    if (curPecs.background) {
      return (
        <PecsWrapper
          alt="pecs"
          src={curPecs.imageId}
          style={{
            background: curPecs.background
          }}
        />
      );
    } 
    return (
      <PecsWrapper
        alt="pecs"
        src={composeIconUrl(curPecs.imageId)}
      />
    );
    
  };

  const renderPecsPreview = () => {
    if (!pecs) return null;
    if (pecs.background) {
      return (
        <PecsPreview
          alt="pecs"
          src={pecs.imageId}
          style={{
            background: pecs.background
          }}
        />
      );
    } 
    return (
      <PecsPreview
        alt="pecs"
        src={composeIconUrl(
          pecsArr.filter(p => p._id === pecs._id)[0].imageId
        )}
      />
    );
    
  };

  /** 
   *  There is an experience problem:
   *   - user can scroll auto complete more than needed  
   */
  return (
    <>
      <AutoComplete
        onSearch={handleSearch}
        placeholder="e.g. Take a shower"
        onSelect={handleSelect}
        onChange={(input: string) => {
          // "_id^pecs^input" -> parts[1] = pecs name
          // "_id^pecs^input" -> parts[2] = input
          const parts = input.split((splitSymbol));

          // exclude pecs from combined value
          setValue(parts[2] ? parts[2] : parts[1]);
        }}
        value={value}
        dropdownClassName={dropdownClassName}
      > 
        {result.map((curPecs: TodoPecs) => (
          <Option key={curPecs.name + curPecs.imageId} value={curPecs.value}>
            {renderPecsWrapper(curPecs)}
            <Description>{curPecs.name}</Description>
          </Option>
        ))}
      </AutoComplete>
      {renderPecsPreview()}
    </>
  );
}

export default observer(ToDoAutoComplete);
